import React from "react";
import { motion } from "framer-motion";

const variants = {
  open: {
    transition: { staggerChildren: 0.07, delayChildren: 0.2 },
  },
  closed: {
    transition: { staggerChildren: 0.05, staggerDirection: -1 },
  },
};

const liVariants = {
  open: {
    y: 0,
    opacity: 1,
    transition: {
      y: { stiffness: 1000, velocity: -100 },
    },
  },
  closed: {
    y: 50,
    opacity: 0,
    transition: {
      y: { stiffness: 1000 },
    },
  },
};

export const Navigation = () => (
  <motion.ul variants={variants}>
    <motion.li
      variants={liVariants}
      whileHover={{ scale: 1.3 }}
      whileTap={{ scale: 0.5 }}
    >
      Home
    </motion.li>
    <motion.li
      variants={liVariants}
      whileHover={{ scale: 1.3 }}
      whileTap={{ scale: 0.5 }}
    >
      Experience
    </motion.li>
    <motion.li
      variants={liVariants}
      whileHover={{ scale: 1.3 }}
      whileTap={{ scale: 0.5 }}
    >
      Education
    </motion.li>
    <motion.li
      variants={liVariants}
      whileHover={{ scale: 1.3 }}
      whileTap={{ scale: 0.5 }}
    >
      Works
    </motion.li>
    <motion.li
      variants={liVariants}
      whileHover={{ scale: 1.3 }}
      whileTap={{ scale: 0.5 }}
    >
      HIRE
    </motion.li>
  </motion.ul>
);
